main {
  padding-bottom: 168px;
  width: 100%;
  height: 100%;
  display: grid;
  gap: 25px;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  justify-content: center;
  align-content: center;
  background-color: rgb(192, 230, 230);
  .win-screen {
    width: 100%;
    margin-top: 200px;
    margin-left: 700px;
    text-align: center;
    height: 47vh;
    font-size: 10rem;
  }
}
